

.quote {
    font-family: 'Amatic SC', cursive;
    font-size: 2.5em;
    line-height: 110%;
    font-weight: 600;
    color: #F2786F
}

.author {
    float: right;
    color: #F2786F;
}

.genbtn {
	box-shadow:inset 0px 13px 0px 0px #ffffff;
	background-color:transparent;
	border-radius:12px;
	border:4px solid #f2786f;
	cursor:pointer;
	color:#f2786f;
	font-family: 'Amatic SC', cursive;
	font-size:30px;
	font-weight:bold;
	padding:1px 8px;
	text-decoration:none;
    text-shadow:0px 0px 0px #c7c7c7;
}
.genbtn:hover {
	background-color:transparent;
}
.genbtn:active {
	position:relative;
	top:1px;
}
.btn-twitter {
	box-shadow:inset 0px 13px 0px 0px #ffffff;
	background-color:transparent;
	border-radius:12px;
	border:4px solid #f2786f;
	cursor:pointer;
	color:#f2786f;
	font-family: 'Amatic SC', cursive;
	font-size:30px;
	font-weight:bold;
	padding:1px 8px;
	text-decoration:none;
    text-shadow:0px 0px 0px #c7c7c7;
}

.btn-twitter:hover {
    border:4px solid #0043af;
}
.quote-box {
    width: 500px;
    margin: auto;
    border-radius: 8;
    margin-top: 10px;
    padding: 20px;
    background-color: #fff;
}

.btn-container {
    width: 100%
}

.btn-container > div {
    display: inline-block;
    margin:auto;
}

.title {
    width: 500px;
    margin: auto;
    text-align: center;
    margin-top: 90px;
    color: white;
    font-family: 'Courier New', Courier, monospace
}

footer {
    width: 500px;
    margin: auto;
    margin-top: 5px;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    color: white;
}

