.btn-twitter {
    -moz-box-shadow:inset 0px 13px 0px 0px #ffffff;
	-webkit-box-shadow:inset 0px 13px 0px 0px #ffffff;
	box-shadow:inset 0px 13px 0px 0px #ffffff;
	background-color:transparent;
	-moz-border-radius:12px;
	-webkit-border-radius:12px;
	border-radius:12px;
	border:4px solid #f2786f;
	cursor:pointer;
	color:#f2786f;
	font-family: 'Amatic SC', cursive;
	font-size:30px;
	font-weight:bold;
	padding:1px 8px;
	text-decoration:none;
    text-shadow:0px 0px 0px #c7c7c7;
}

.btn-twitter:hover {
    border:4px solid #0043af;
}